<template>
  <template v-if="!items.length">
    <div class="flex items-center justify-center py-6">
      <h6 class="text-lg font-semibold text-grey-04">No data available</h6>
    </div>
  </template>
  <div class="changelog-timeline">
    <div v-for="(item, itemIndex) of items" :key="itemIndex" :class="[`changelog-group`, `${item.isCollapse ? '--collapse' : ''}`]">
      <div class="changelog-group__item">
        <div class="w-full xl:w-1/6 lg:w-2/6 md:w-2/6">
            <div @click="item.isCollapse = !item.isCollapse" class="changelog-action">
              <span v-if="dateKey" class="mr-2">{{item[dateKey]}}</span>
              <span v-else><slot name="action" :item="item"></slot></span>
              <img class="changelog-action__icon" src="@/assets/icon/arrow-circle-invert.svg" alt="arrow">
            </div>
        </div>
        <div class="w-full xl:w-5/6 lg:w-4/6 md:w-4/6 border-l border-teal-04">
          <div class="changelog-group-dot"></div>
        </div>
      </div>
      <template v-if="childKey">
        <div v-for="(child, childIndex) of item[childKey]" :key="childIndex" class="changelog-group__item">
          <div class="w-full xl:w-1/6 lg:w-2/6 md:w-2/6 my-4">
            <slot :item="child" :parent="item" name="description"></slot>
          </div>
          <div class="w-full xl:w-5/6 lg:w-4/6 md:w-4/6 border-l border-teal-04">
            <div class="changelog-item">
              <div class="flex">
                <div class="changelog-dot"></div>
                <div>
                  <slot :item="child" :parent="item">{{child}}</slot>
                </div>
              </div>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref, watchEffect } from 'vue'

export default defineComponent({
  name: 'Changelog',
  components: {
  },
  props: {
    items: {
      type: Array,
      default: () => ([])
    },
    collapse: {
      type: Boolean,
      default: false
    },
    childKey: {
      type: String,
      default: ''
    },
    dateKey: {
      type: String,
      default: ''
    }
  },
  setup(props, {emit}) {
    const items = ref([])
    watchEffect(() => {
      items.value = props.items.map((item) => ({...item, isCollapse: props.collapse}))
    })
    return {
      items
    }
  }
})
</script>

<style lang="scss" scoped>
.changelog-group{
  @apply transition-all duration-300;
  max-height: 99999999px;
  .changelog-action{
    @apply cursor-pointer inline-flex items-center bg-teal-04 px-4 py-2 inline-block text-white font-semibold rounded-full;
    .changelog-action__icon{
      @apply transition-transform duration-300;
    }
  }
  &:not(.--collapse){
    max-height: 40px;
    overflow: hidden;
  }
  &.--collapse{
    .changelog-action{
      .changelog-action__icon{
        @apply transform rotate-180;
      }
    }
  }
}
.changelog-group__item{
  @apply flex;
  .changelog-group-dot{
    @apply w-2 h-2 rounded-full p-4 bg-teal-04 transform -translate-x-4 transition-transform duration-300 cursor-pointer;
    &:hover{
      @apply scale-110;
    }
  }
  .changelog-dot{
    @apply flex items-center justify-center p-3 w-2 h-2 rounded-full bg-teal-04 relative transform -translate-x-3 transition-transform duration-300;
    &:hover{
      @apply scale-110 cursor-pointer;
    }
    &:before{
      @apply absolute w-1 h-1 p-1 rounded-full bg-white;
      content: "";
    }
  }
  .changelog-item{
    @apply my-4;
  }
}
</style>